/* button hover effect */

.b1 {
  margin-top: 40px;
  background: #1aab8a;
  color: #fff;
  border: none;
  border-radius: 5px;
  position: relative;
  height: 50px;
  font-size: 1.6em;
  padding: 0 2em;
  cursor: pointer;
  transition: 500ms ease all;
  outline: none;
}
.b1:hover {
  background: #fff;
  color: #1aab8a;
}
.b1:before,
.b1:after {
  content: '';
  position: absolute;
  border-radius: 5px;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #1aab8a;
  transition: 400ms ease all;
}
.b1:after {
  right: inherit;
  top: inherit;
  border-radius: 5px;
  left: 0;
  bottom: 0;
}
.b1:hover:before,
.b1:hover:after {
  width: 100%;
  border-radius: 5px;

  transition: 800ms ease all;
}

